import {
  Box,
  Drawer,
  useMediaQuery,
  useTheme
} from '@mui/material';
import React from 'react';
import Navigation from '../Navigation/Navigation';

interface SidebarProps {
  isMobile?: boolean;
  isOpen?: boolean;
  onClose?: () => void;
}

const Sidebar: React.FC<SidebarProps> = ({ isMobile = false, isOpen = false, onClose }) => {
  const theme = useTheme();
  const isMobileDevice = useMediaQuery(theme.breakpoints.down('md'));
  const showMobileView = isMobileDevice || isMobile;

  const sidebarContent = (
    <>
      <Navigation isMobileView={showMobileView} onMobileClose={onClose} />
    </>
  );

  if (showMobileView) {
    return (
      <Drawer
        anchor="left"
        open={isOpen}
        onClose={onClose}
        sx={{
          '& .MuiDrawer-paper': {
            width: '100%',
            boxSizing: 'border-box',
            bgcolor: '#ffffff',
            animation: isOpen ? 'slideIn 0.3s forwards' : 'none',
          },
          '@keyframes slideIn': {
            '0%': {
              transform: 'translateX(-100%)'
            },
            '100%': {
              transform: 'translateX(0)'
            }
          }
        }}
      >
        {sidebarContent}
      </Drawer>
    );
  }

  return (
    <Box 
      sx={{ 
        width: 280,
        flexShrink: 0, 
        borderRight: '1px solid rgba(0, 0, 0, 0.08)',
        backgroundColor: '#fff',
        display: { xs: 'none', md: 'block' }
      }}
    >
      {sidebarContent}
    </Box>
  );
};

export default Sidebar;
