import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  IconButton, 
  Switch, 
  Pagination, 
  PaginationItem,
  useMediaQuery,
  useTheme
} from '@mui/material';
import FavoriteIcon from '@mui/icons-material/FavoriteBorder';
import FavoriteFilledIcon from '@mui/icons-material/Favorite';
import MenuIcon from '@mui/icons-material/Menu';
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

interface Job {
  id: number;
  title: string;
  company: string;
  companyLogo: string;
  location: string;
  timePosted: string;
  experience: string;
  jobType: string;
  remoteType: string;
  categories: string[];
}

const JobListings: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [currentPage, setCurrentPage] = useState(2);
  const [favoriteJobs, setFavoriteJobs] = useState<number[]>([]);
  
  const toggleFavorite = (jobId: number) => {
    if (favoriteJobs.includes(jobId)) {
      setFavoriteJobs(favoriteJobs.filter(id => id !== jobId));
    } else {
      setFavoriteJobs([...favoriteJobs, jobId]);
    }
  };
  
  const handleChangePage = (event: React.ChangeEvent<unknown>, value: number) => {
    setCurrentPage(value);
  };
  
  const jobData: Job[] = [
    {
      id: 1,
      title: "Gaming UI designer",
      company: "Rockstar Games",
      companyLogo: "#4285F4",
      location: "ElMansoursa, Egypt",
      timePosted: "10 days ago",
      experience: "0 - 3y of exp",
      jobType: "Full time",
      remoteType: "Remote",
      categories: ["Creative / Design", "IT / Software development", "Gaming"]
    },
    {
      id: 2,
      title: "Senior UX UI Designer",
      company: "Egabi",
      companyLogo: "#0FB5EE",
      location: "Cairo, Egypt",
      timePosted: "10 days ago",
      experience: "0 - 3y of exp",
      jobType: "Full time",
      remoteType: "Hybrid",
      categories: ["Creative / Design", "IT / Software development"]
    },
    {
      id: 3,
      title: "React Frontend developer",
      company: "Megara",
      companyLogo: "#7628EE",
      location: "Cairo, Egypt",
      timePosted: "10 days ago",
      experience: "5 - 7y of exp",
      jobType: "Freelance",
      remoteType: "Remote",
      categories: ["Creative / Design", "IT / Software development"]
    }
  ];

  return (
    <Box sx={{ width: '100%', height: '100%', bgcolor: '#FFFFFF' }}>
      <Box sx={{ 
        bgcolor: '#4CAF50', 
        color: 'white',
        p: isMobile ? 2 : 3,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <Box>
          <Typography variant="h6" component="h1" sx={{ fontWeight: 500 }}>
            UI Designer in Egypt
          </Typography>
          <Typography variant="body2" sx={{ mt: 0.5 }}>
            70 job positions
          </Typography>
        </Box>
        
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <Typography variant="body2" sx={{ display: { xs: 'none', sm: 'block' } }}>
            Set alert
          </Typography>
          <Switch size="small" color="default" />
          
          {isMobile && (
            <IconButton sx={{ color: 'white', ml: 1 }}>
              <MenuIcon />
            </IconButton>
          )}
        </Box>
      </Box>
      
      <Box sx={{ p: isMobile ? 0 : 2 }}>
        {jobData.map((job) => (
          <Box
            key={job.id}
            sx={{
              p: 2,
              borderBottom: '1px solid',
              borderColor: 'rgba(0, 0, 0, 0.08)',
              display: 'flex',
              flexDirection: 'column',
              gap: 1.5
            }}
          >
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <Box 
                  sx={{ 
                    width: 40, 
                    height: 40, 
                    bgcolor: job.companyLogo,
                    borderRadius: 1,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white',
                    fontSize: 20,
                    fontWeight: 'bold'
                  }}
                >
                  {job.company.charAt(0)}
                </Box>
                
                <Box>
                  <Typography variant="subtitle1" sx={{ fontWeight: 500, mb: 0.5 }}>
                    {job.title}
                  </Typography>
                  <Typography variant="body2" color="primary" sx={{ fontWeight: 500, mb: 0.5 }}>
                    {job.company}
                  </Typography>
                  
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5, color: 'text.secondary' }}>
                    <LocationOnOutlinedIcon sx={{ fontSize: 14 }} />
                    <Typography variant="caption">
                      {job.location}
                    </Typography>
                    
                    <Box component="span" sx={{ mx: 0.5 }}></Box>
                    
                    <AccessTimeIcon sx={{ fontSize: 14 }} />
                    <Typography variant="caption">
                      {job.timePosted}
                    </Typography>
                  </Box>
                </Box>
              </Box>
              
              <IconButton
                onClick={() => toggleFavorite(job.id)}
                size="small"
                sx={{ alignSelf: 'flex-start', color: '#999' }}
              >
                {favoriteJobs.includes(job.id) ? 
                  <FavoriteFilledIcon sx={{ color: '#F44336' }} /> : 
                  <FavoriteIcon />
                }
              </IconButton>
            </Box>
            
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
              <Box sx={{ 
                bgcolor: 'rgba(0, 0, 0, 0.04)', 
                px: 1, 
                py: 0.5, 
                borderRadius: 0.5,
                fontSize: 12
              }}>
                {job.experience}
              </Box>
              <Box sx={{ 
                bgcolor: 'rgba(0, 0, 0, 0.04)', 
                px: 1, 
                py: 0.5, 
                borderRadius: 0.5,
                fontSize: 12
              }}>
                {job.jobType}
              </Box>
              <Box sx={{ 
                bgcolor: 'rgba(0, 0, 0, 0.04)', 
                px: 1, 
                py: 0.5, 
                borderRadius: 0.5,
                fontSize: 12
              }}>
                {job.remoteType}
              </Box>
            </Box>
            
            <Typography variant="caption" color="text.secondary" sx={{ display: 'flex', gap: 1 }}>
              {job.categories.map((category, index) => (
                <React.Fragment key={index}>
                  {category}
                  {index < job.categories.length - 1 && (
                    <Box component="span">-</Box>
                  )}
                </React.Fragment>
              ))}
            </Typography>
          </Box>
        ))}
      </Box>
      
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 2 }}>
        <Pagination
          count={5}
          page={currentPage}
          onChange={handleChangePage}
          renderItem={(item) => (
            <PaginationItem
              {...item}
              sx={{
                '&.Mui-selected': {
                  bgcolor: '#4CAF50',
                  color: 'white',
                  '&:hover': {
                    bgcolor: '#43A047',
                  }
                }
              }}
            />
          )}
        />
      </Box>
    </Box>
  );
};

export default JobListings; 